import { Button, List, Modal } from "antd"
import { Row, ScreenContainer } from "compoents/lib"
import dayjs from "dayjs"
import { useState } from "react"
import { Link } from "react-router-dom"
import { useProjectInUrl } from "screens/kanban/util"
import { Epic } from "types/epic"
import { useDeleteEpic, useEpics } from "untils/epic"
import { useTasks } from "untils/task"
import { CreateEpic } from "./create-epic"
import { useEpicSearchParams, useEpicsQueryKey } from "./until"

const EpicScreen=()=>{
    const {data:currentProject}=useProjectInUrl()
    const {data:epics}=useEpics(useEpicSearchParams())
    const {data:tasks}=useTasks({projectId:currentProject?.id})
    const {mutate:deleteEpic}=useDeleteEpic(useEpicsQueryKey())
    const [epicCreateOpen,setEpicCreateOpen]=useState(false)

    const startDelete=(epic:Epic)=>{
        Modal.confirm({
            okText:'确定',
            cancelText:'取消',
            title:`确定删除项目组${epic.name}`,
            onOk(){
              return deleteEpic({id:epic.id})
            }
          })
    }
    return <ScreenContainer>
        <Row between={true}>
            <h1>{currentProject?.name}任务组</h1>
            <Button type={'link'} onClick={()=>setEpicCreateOpen(true)}>创建任务组</Button>
        </Row>

        <List style={{overflow:'scroll'}} dataSource={epics} itemLayout={'vertical'} renderItem={
            epic=><List.Item>
                <List.Item.Meta 
                    title={<Row between={true}>
                            <span>{epic.name}</span>
                            <Button type={'link'} onClick={()=>startDelete(epic)}>删除</Button>
                        </Row>}
                    description={<div>
                        <div>开始时间：{dayjs(epic.start).format('YYYY-MM-DD')}</div>
                        <div>结束时间：{dayjs(epic.end).format('YYYY-MM-DD')}</div>
                    </div>}
                />
                <div>
                    {tasks?.filter(task=>task.epicId===epic.id).map(task=><Link to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`} key={task.id}>{task.name}</Link>)}
                </div>
            </List.Item>
        }/>
        <CreateEpic onClose={()=>setEpicCreateOpen(false)} visible={epicCreateOpen}/>
    </ScreenContainer>
}
export default EpicScreen