import styled from "@emotion/styled"
import { Divider, List, Popover, Typography } from "antd"
import { ButtonNoPadding } from "compoents/lib"
import {useProjects} from "untils/project"
import { useProjectModal } from "./util"


export const ProjectPopover=()=>{
    //打开ProjectModal的方法
    const {open}=useProjectModal()
    //获取所有的project信息，和重新查询函数
    const {data:projects,refetch}=useProjects()
    //筛选已收藏项目
    const pinPorjects=projects?.filter(project=>project.pin)
    const content=<Container>
        <Typography.Text type={"secondary"}>收藏项目</Typography.Text>
        <List>
            {pinPorjects?.map(project=><List.Item key={project.name}>
                <List.Item.Meta title={project.name}/>
            </List.Item>)}
        </List>
        <Divider/>
        <ButtonNoPadding type={'link'} onClick={open}>创建项目</ButtonNoPadding>
    </Container>
    return <Popover onVisibleChange={()=>refetch()} placement={"bottom"} content={content}>
       <span>项目</span>
    </Popover>
}
const Container=styled.div`
    min-width: 30rem;
`