import React from "react";
import { Kanban } from "types/kanban";
import { useTasks } from "untils/task";
import { useKanbansQueryKey, useTasksModal, useTasksSearchParams } from "./util";
import bugIcon from "assets/bug.svg";
import taskIcon from "assets/task.svg"
import { useTaskTypes } from "untils/task-type";
import styled from "@emotion/styled";
import { Button, Card, Dropdown, Menu, Modal } from "antd";
import { CreateTask } from "./create-task";
import { Task } from "types/task";
import { Mark } from "compoents/mark";
import { useDeleteKanban } from "untils/kanban";
import { Row } from "compoents/lib";
import { Drag, Drop, DropChild } from "compoents/drag-and-drop";

const TaskTypeIcon=({id}:{id:number})=>{
    const {data:taskTypes}=useTaskTypes()
    const name=taskTypes?.find(taskType=>taskType.id===id)?.name
    if(!name)
        return null
    return <img alt="task" src={name==='task' ? taskIcon : bugIcon}/>
}

const TaskCard=({task}:{task:Task})=>{
  const {startEdit}=useTasksModal()
  const {name:keyword}=useTasksSearchParams()
  return <Card onClick={()=>startEdit(task.id)} style={{ marginBottom: '0.5rem',cursor:'pointer'}}>
    <p>
      <Mark name={task.name} keyword={keyword||''}/>
    </p>
    <TaskTypeIcon id={task.typeId}/>
</Card>
}

export const More=({kanban}:{kanban:Kanban})=>{
    const {mutateAsync:deleteKanban}=useDeleteKanban(useKanbansQueryKey())
    const startEdit=()=>{
      Modal.confirm({
        okText:'确定',
        cancelText:'取消',
        title:'确定删除看板吗',
        onOk(){
          return deleteKanban({id:kanban.id})
        }
      })
    }
    const overlay=<Menu>
      <Menu.Item>
          <Button type={'link'} onClick={startEdit}>删除</Button>
      </Menu.Item>
    </Menu>
    return <Dropdown overlay={overlay}>
      <Button type={'link'}>...</Button>
    </Dropdown>
}
export const KanbanColumn=React.forwardRef<HTMLDivElement,{kanban:Kanban}>(({kanban,...props},ref)=>{
  const {data:allTasks}=useTasks(useTasksSearchParams())
  const tasks=allTasks?.filter(task=>task.kanbanId===kanban.id)
  return <Container {...props} ref={ref}>
  <Row between={true}>
    <h3>{kanban.name}</h3>
    <More kanban={kanban} key={kanban.id} />
  </Row>
  <TasksContainer>
    <Drop
      type={"ROW"}
      direction={"vertical"}
      droppableId={String(kanban.id)}
    >
      <DropChild style={{ minHeight: "1rem" }}>
        {tasks?.map((task, taskIndex) => (
          <Drag
            key={task.id}
            index={taskIndex}
            draggableId={"task" + task.id}
          >
            <div>
              <TaskCard key={task.id} task={task} />
            </div>
          </Drag>
        ))}
      </DropChild>
    </Drop>
    <CreateTask kanbanId={kanban.id} />
  </TasksContainer>
</Container>
})

export const Container = styled.div`
  min-width: 27rem;
  border-radius: 6px;
  background-color: rgb(244, 245, 247);
  display: flex;
  flex-direction: column;
  padding: 0.7rem 0.7rem 1rem;
  margin-right: 1.5rem;
`;

const TasksContainer = styled.div`
  overflow: scroll;
  flex: 1;

  ::-webkit-scrollbar {
    display: none;
  }
`;
