import { useMemo } from "react"
import { URLSearchParamsInit, useSearchParams } from "react-router-dom"
import { clearnObject } from "untils"

//返回页面url中，指定键的参数，和设置url参数的方法
export const useUrlQueryParam=<K extends string>(keys:K[])=>{
    //获取url搜索参数
    const [searchParam]=useSearchParams()
    //设置url
    const setSearchParams=useSetUrlSearchParam()
    return [
        useMemo(()=>keys.reduce((pre,key)=>{
            return {...pre,[key]:searchParam.get(key)||''}
    },{} as {[key in K]:string}),[searchParam])
        ,(params:Partial<{[key in K]:unknown}>)=>{
            return setSearchParams(params)
    }] as const
}

//返回设置url的方法
export const useSetUrlSearchParam=()=>{
    //使用useSearchParams获取url搜索参数，和设置它的方法
    const [searchParam,setSearchParam]=useSearchParams()

    return (params:{[key in string]:unknown})=>{
        //格式化对象
        const o=clearnObject({...Object.fromEntries(searchParam),...params}) as URLSearchParamsInit
        return setSearchParam(o)
    }
}
