import { Button, Dropdown, Menu, Modal, Table, TableProps } from 'antd'
import { ButtonNoPadding } from 'compoents/lib'
import { Pin } from 'compoents/pin'
import dayjs from 'dayjs'
import { Link } from 'react-router-dom'
import { useDeleteProject, useEditProject } from 'untils/project'
import { useDeleteConfig } from 'untils/use-optimistic-options'
import { Project } from '../../types/project'
import { User } from "../../types/uer"
import { useProjectModal, useProjectsQueryKey } from './util'
interface ListProps extends TableProps<Project>{
    users:User[],
}
const List=({users,...props}:ListProps)=>{
    const {mutate}=useEditProject(useProjectsQueryKey())

    const pinProject=(id:number)=>(pin:boolean)=>mutate({id,pin})

    return <Table 
        rowKey={"id"}
        pagination={false} 
        columns={[
            {
                title:<Pin checked={true} disabled={true}/>,
                render(value,project){
                    return <Pin checked={project.pin} onCheckedChange={pinProject(project.id)}/>
                }
            },
            {
                title:'名称',
                sorter:(a,b)=>a.name.localeCompare(b.name),
                render(project){
                    return <Link to={`projects/${String(project.id)}`}>{project.name}</Link>
                }
            },
            {
                title:'部门',
                dataIndex:'organization',
            },
            {
                title:'负责人',
                render(project){
                    return <span>
                        {users.find((user:User)=>user.id===project.personId)?.name}
                    </span>
                }
            },
            {
                title:'创建事件',
                render(project){
                    return <span>
                        {project.created?dayjs(project.created).format('YYYY-MM-DD'):'无'}
                    </span>
                }
            },{
                render(value,project){
                    return <More project={project}/>
                }
            }
        ]} {...props}/>
}
const More=({project}:{project:Project})=>{
    const {startEidt}=useProjectModal()
    const editProject=(id:number)=>()=>startEidt(id)
    const {mutate:deleteProject}=useDeleteProject(useProjectsQueryKey())
    const confirmDeleteProject=(id:number)=>{
        Modal.confirm({
            title:'确定删除这个项目?',
            content: '点击确定删除',
            okText:'确定',
            onOk(){
                deleteProject({id})
            }
        })
    }
    return <Dropdown overlay={<Menu>
        <Menu.Item onClick={editProject(project.id)} key={'edit'}>
            编辑
        </Menu.Item>
        <Menu.Item onClick={()=>confirmDeleteProject(project.id)} key={'delete'}>
            删除
        </Menu.Item>
    </Menu>}>
        <ButtonNoPadding type={'link'}>...</ButtonNoPadding>
    </Dropdown>
}
export default List