import { useMemo } from "react"
import { useSearchParams } from "react-router-dom"
import { useProject } from "untils/project"
import { useSetUrlSearchParam, useUrlQueryParam } from "untils/url"
//项目列表搜索的参数
export const useProjectsSearchParams=()=>{
    //url中name，和personId的参数，和设置他们的方法
    const [param,setParam]=useUrlQueryParam(['name','personId'])
    //将personId转成Number类型
    return [useMemo(()=>({...param,personId:Number(param.personId)||undefined}),[param]),setParam] as const
}
//返回React-query用于查询的key
export const useProjectsQueryKey=()=>{
    const [params]=useProjectsSearchParams()
    return ['projects',params]
}

//获取ProjectModal的状态和操作方法，通过url来管理ProjectModal的状态
export const useProjectModal=()=>{
    //获取url参数中projectCreate的键值对象，和方法
    const [{projectCreate},setProjectCreate]=useUrlQueryParam(['projectCreate'])
    //获取正在修改项目的id
    const [{editingProjectId},setEditingProjectId]=useUrlQueryParam(['editingProjectId'])
    //设置url的方法
    const setUrlParams=useSetUrlSearchParam()
    //从react-query中获取正在修改的project信息和loading状态
    const {data:editingProject,isLoading}=useProject(Number(editingProjectId))
    //打开的时候将参数添加到url上
    const open=()=>setProjectCreate({projectCreate:true})
    //关闭的时候清除url
    const close=()=>setUrlParams({projectCreate:'',editingProjectId:''})
    //将要修改的projectId添加到url
    const startEidt=(id:number)=>setEditingProjectId({editingProjectId:id})

    return {
        projectModalOpen:projectCreate==='true'||Boolean(editingProjectId),
        open,
        close,
        startEidt,
        editingProject,
        isLoading
    }
        
    
}