import { Form, Input, Select } from "antd"
import { UserSelect } from "compoents/user-select"
import { Project } from "types/project"
import { User } from "../../types/uer"

interface SearchPanelProps{
    param:Partial<Pick<Project,'name'|'personId'>>,
    setParam:(param:SearchPanelProps['param'])=>void,
    users:User[]
}
const SearchPanel = (props:SearchPanelProps) => { 
    const {param,setParam}=props 
    return (
        <Form style={{marginBottom :'2rem'}} layout={"inline"}>
            <Form.Item>
                <Input placeholder="项目名" value={param.name} type="text"  onChange={(e)=>setParam({...param,name:e.target.value})
            } />
            </Form.Item>
            <Form.Item>
                <UserSelect 
                    defaultOptionName={"负责人"}
                    value={param.personId} onChange={value => {
                    setParam({ ...param, personId: value })
                }}/>
            </Form.Item>      
        </Form>
    )
}
export default SearchPanel