import { Button, Input } from "antd"
import { Row } from "compoents/lib"
import { TaskTypeSelect } from "compoents/task-type-select"
import { UserSelect } from "compoents/user-select"
import { useSetUrlSearchParam } from "untils/url"
import { useTasksSearchParams } from "./util"

export const SearchPanel=()=>{
    const searchParam=useTasksSearchParams()
    const setSearchParam=useSetUrlSearchParam()
    const reset=()=>{
        setSearchParam({
            typeId:undefined,
            name:undefined,
            processorId:undefined,
            tagId:undefined
        })
    }
    return <Row marginBottom={4} gap={true}>
        <Input style={{width:'20rem'}} placeholder={`任务名`} value={searchParam.name} onChange={e=>setSearchParam({name:e.target.value})}/>
        <UserSelect defaultOptionName={`经办人`} value={searchParam.processorId} onChange={value=>setSearchParam({processorId:value})}/>
        <TaskTypeSelect defaultOptionName={`类型`} value={searchParam.typeId} onChange={value=>setSearchParam({typeId:value})}/>
        <Button onClick={reset}>清除筛选器</Button>
    </Row>  
}