import styled from '@emotion/styled'
import { ButtonNoPadding, Row } from 'compoents/lib'
import { useAuth } from 'context/auth-context'
import ProjectListScreen from 'screens/project-list'
import {ReactComponent as SoftwareLog} from 'assets/software-logo.svg'
import { Button, Dropdown, Menu } from 'antd'
import { resetRoute, useDocumentTitle } from 'untils'
import {BrowserRouter as Router,Route,Routes } from 'react-router-dom'
import ProjectScreen from 'screens/project'
import { useState } from 'react'
import { ProjectModal } from 'screens/project-list/project-modal'
import { ProjectPopover } from 'screens/project-list/project-popover'
import { UserPopover } from 'compoents/user-popover'
const AuthenticatedApp=()=>{
    //更改title
    useDocumentTitle('项目列表',false)
    return ( 
        <Container>
            <Router>
                <PageHeader/>
                <Main>    
                    <Routes>
                        <Route path={`projects`} element={<ProjectListScreen/>} />
                        <Route path={`projects/:projectId/*`} element={<ProjectScreen/>}/> 
                        <Route index element={<ProjectListScreen/>}/>
                    </Routes>    
                </Main>
                <ProjectModal/>
            </Router>
        </Container>
    )
}

const PageHeader=()=>{
    return <Header between={true}>
    <HeaderLeft gap={true}>
        {/* 图标链接，点击可以回到首页 */}
        <ButtonNoPadding type={`link`} onClick={resetRoute}><SoftwareLog width={'18rem'} color={'rgb(38,132,255)'}/></ButtonNoPadding> 
        <ProjectPopover/>
        <UserPopover/>
    </HeaderLeft>
    <HeaderRight>
        <User/>
    </HeaderRight>   
</Header>
}

const User=()=>{
    const {logout,user}=useAuth()
    return <Dropdown overlay={
        <Menu>
            <Menu.Item key={'logout'}>
                <Button type={`link`} onClick={logout}>登出</Button>
            </Menu.Item>
        </Menu>}>
            <Button type={`link`} onClick={e=>e.preventDefault()}>Hi, {user?.name}</Button>
    </Dropdown>
}

const Container=styled.div`
    display:grid;
    grid-template-rows: 6rem 1fr;
    height: 100vh;`

const Header=styled(Row)`
    padding: 3.2rem;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.1);
    z-index: 1;
`

const HeaderLeft=styled(Row)``

const HeaderRight=styled.div``

const Main=styled.main`
    display: flex;
    overflow: hidden;
`

export default AuthenticatedApp