import List from './list'
import SearchPanel from './search-panel'
import { useDebounce, useDocumentTitle } from 'untils/index'
import {useProjects} from 'untils/project'
import useUsers from 'untils/user'
import { useProjectModal, useProjectsSearchParams } from './util'
import { ButtonNoPadding, ErrorBox, Row, ScreenContainer } from 'compoents/lib'
import { Profiler } from 'compoents/profiler'

const ProjectListScreen = () => {
    //更该title
    useDocumentTitle("项目列表",false)
    //获取ProjectModal的打开方法
    const {open}=useProjectModal()
    //获取url中的参数，和设置url的方法
    const [param,setParam]=useProjectsSearchParams()
    //获取所有的user信息
    const {data:users}=useUsers()
    //防抖查询搜索框对应的内容
    const {error,isLoading,data:list}=useProjects(useDebounce(param, 200))
    return (
        <Profiler id={'项目列表'}>
            <ScreenContainer>
                <Row between={true} marginBottom={2}>
                    <h1>项目列表</h1>
                    <ButtonNoPadding type={'link'} onClick={open}>创建项目</ButtonNoPadding>
                </Row>
                
                <SearchPanel users={users||[]} param={param} setParam={setParam} />
                {error ? <ErrorBox error={error}/> : null}
                <List dataSource={list||[]} users={users||[]} loading={isLoading} />
            </ScreenContainer>
        </Profiler>
    )
}

export default ProjectListScreen