import styled from "@emotion/styled"
import { Spin } from "antd"
import { ScreenContainer } from "compoents/lib"
import { useDocumentTitle } from "untils"
import { useKanbans, useReorderKanban } from "untils/kanban"
import { useReorderTask, useTasks } from "untils/task"
import { CreateKanban } from "./create-kanban"
import { KanbanColumn } from "./kanban-column"
import { SearchPanel } from "./search-panel"
import { TaskModal } from "./task-modal"
import { useKanbansQueryKey, useKanbansSearchParams, useProjectInUrl, useTasksQueryKey, useTasksSearchParams } from "./util"
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { Drag, Drop, DropChild } from "compoents/drag-and-drop"
import { useCallback } from "react"
import { Profiler } from "compoents/profiler"
const KanbanScreen=()=>{
    useDocumentTitle('看板列表')
    const {data:kanbans,isLoading:kanbanIsLoading}=useKanbans(useKanbansSearchParams())
    const {data:currentProject}=useProjectInUrl()
    const {isLoading:taskIsLoading}=useTasks(useTasksSearchParams())
    const isLoading=kanbanIsLoading||taskIsLoading
    const onDragEnd = useDragEnd();
    return (
      <Profiler id={'看板页面'}>
        <DragDropContext onDragEnd={onDragEnd}>
          <ScreenContainer>
            <h1>{currentProject?.name}看板</h1>
            <SearchPanel />
            {isLoading ? (
              <Spin size={"large"} />
            ) : (
              <ColumnsContainer>
                <Drop
                  type={"COLUMN"}
                  direction={"horizontal"}
                  droppableId={"kanban"}
                >
                  <DropChild style={{ display: "flex" }}>
                    {kanbans?.map((kanban, index) => (
                      <Drag
                        key={kanban.id}
                        draggableId={"kanban" + kanban.id}
                        index={index}
                      >
                        <KanbanColumn kanban={kanban} key={kanban.id} />
                      </Drag>
                    ))}
                  </DropChild>
                </Drop>
                <CreateKanban />
              </ColumnsContainer>
            )}
            <TaskModal />
          </ScreenContainer>
        </DragDropContext>
      </Profiler>
    )
}

export const useDragEnd = () => {
    const { data: kanbans } = useKanbans(useKanbansSearchParams());
    const { mutate: reorderKanban } = useReorderKanban(useKanbansQueryKey());
    const { mutate: reorderTask } = useReorderTask(useTasksQueryKey());
    const { data: allTasks = [] } = useTasks(useTasksSearchParams());
    return useCallback(
      ({ source, destination, type }: DropResult) => {
        if (!destination) {
          return;
        }
        // 看板排序
        if (type === "COLUMN") {
          const fromId = kanbans?.[source.index].id;
          const toId = kanbans?.[destination.index].id;
          if (!fromId || !toId || fromId === toId) {
            return;
          }
          const type = destination.index > source.index ? "after" : "before";
          reorderKanban({ fromId, referenceId: toId, type });
        }
        if (type === "ROW") {
          const fromKanbanId = +source.droppableId;
          const toKanbanId = +destination.droppableId;
          const fromTask = allTasks.filter(
            (task) => task.kanbanId === fromKanbanId
          )[source.index];
          const toTask = allTasks.filter((task) => task.kanbanId === toKanbanId)[
            destination.index
          ];
          if (fromTask?.id === toTask?.id) {
            return;
          }
          reorderTask({
            fromId: fromTask?.id,
            referenceId: toTask?.id,
            fromKanbanId,
            toKanbanId,
            type:
              fromKanbanId === toKanbanId && destination.index > source.index
                ? "after"
                : "before",
          });
        }
      },
      [kanbans, reorderKanban, allTasks, reorderTask]
    );
  };
  
export const ColumnsContainer=styled('div')`
    display: flex;
    overflow-x: scroll;
    flex: 1;
`
export default KanbanScreen